@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
}
body {
  background: rgb(255,175,189);
  background: linear-gradient(270deg, rgba(255,175,189,1) 0%, rgba(201,255,191,1) 100%);
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.feed {
  min-height: 100vh;
  width: 1000px;
  padding: 12.5px 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 12.5px;
  gap: 12.5px;
  border-left: 1px solid rgba(101, 101, 101, 0.5);
  border-right: 1px solid rgba(101, 101, 101, 0.5);
}
/* font-family: 'Prompt', sans-serif; */
/* font-family: 'Roboto', sans-serif; */
.post-container {
    width: 750px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    padding-top: 12.5px;
    background: rgba(255, 255, 255, 0.2);
    border: 1px solid rgba(101, 101, 101, 0.5);
    border-radius: 12.5px;
}
.post-container:hover{
    background: rgba(255, 255, 255, 0.5);
}
.profile-picture {
    min-width: 100px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
.profile-picture--container {
    background-color: white;
    border-radius: 50%;
    width: 75px;
    height: 75px;
}
.profile-picture--container img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
}
.profile-picture--container img:hover {
    cursor: pointer;
    -webkit-filter: brightness(0.8);
            filter: brightness(0.8);
}
.post {
    padding-right: 12.5px;
    font-size: 16px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}
.header-info{
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: baseline;
            align-items: baseline;
    grid-gap: 5px;
    gap: 5px;
    width: 100%;
    font-family: 'Poppins', sans-serif;
}
.header-info h3 {
    font-size: 16px;
}
.header-info h3:hover {
    text-decoration: underline;
    cursor: pointer;
}
.header-info p {
    font-family: 'Ubuntu', sans-serif;
    font-size: 14px;
    color: rgb(101, 101, 101);
}
#verified-icon {
    width: 15px;
    height: 15px;
    -webkit-align-self: center;
            align-self: center;
}
#navigate-arrow {
    position: absolute;
    right: 0;
    width: 15px;
    height: 15px;
    -webkit-align-self: center;
            align-self: center;
    fill: rgb(101, 101, 101);
}
#navigate-arrow:hover {
    cursor: pointer;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    transition: 0.3s linear;
}
.content {
    font-family: 'Poppins', sans-serif;
    margin: 5px 0;
    width: 100%;
}
.post-image {
    margin: 10px 0;
}
.post-image img {
    border-radius: 15px;
    max-width: 100%; 
    height: auto; 
    display: block;
}
.post-image img:hover {
    cursor: pointer;
}
.post-activity {
    width: 100%;
    font-family: 'Ubuntu', sans-serif;
    font-size: 14px;
    color: rgb(101, 101, 101);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    margin-bottom: 10px;
}
.likes, .comments, .reposts {
    display: -webkit-flex;
    display: flex;
    grid-gap: 7.5px;
    gap: 7.5px;
    -webkit-align-items: center;
            align-items: center;
}
#like, #comment, #repost, #share {
    -webkit-filter: brightness(0.8);
            filter: brightness(0.8);
    width: 20px;
    height: 20px;
    transition: all 0.3s ease-in-out;
}
#like:hover, #comment:hover, #repost:hover, #share:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    cursor: pointer;
    -webkit-filter: unset;
            filter: unset;
}
